/* 列表样式 */

// 列表字体
.item {
  .hairline-top:before, .hairline-bottom:after {
    background-color: $default-border-color;
  }
}

// .hl-item {
//   .hairline-bottom:after {
//     visibility: hidden;
//   }
// }

.item-note {
  color: #b5b5b5;
}

// 列表右侧箭头
.item.item-icon-right {
  .icon.ion-ios-arrow-right {
    position: absolute;
    right: 20px;
    width: 8px;
    height: 50px;
    &::before {
      content: ' ';
      @include background-image(url(//qianka.b0.upaiyun.com/images/93a893d4236c8b1c3b33100b52046765.png));
      width: 8px;
      height: 12px;
      -webkit-transform: translate3d(0,-4%,0);
    }
  }
}



