@import "../../scss/variables";
@import "../../scss/mixins";

// 任务助手未开启，有更新，已失效
.popup-open-assistant,
.popup-upgrade-assistant,
.popup-invalid-assistant {
  position: relative;
  text-align: center;

  .popup-body {
    padding: 10px 10px 0px !important;
    overflow: hidden !important;
  }

  .icon {
    margin-top: 4px;
    margin-bottom: 30px;
    height: 52px;

    &.icon-off {
      @include background-image(url(//qianka.b0.upaiyun.com/images/a1ca510728cf72f5cd62c28f00e315c4.png), 70px 52px, center);
    }

    &.icon-download {
      @include background-image(url(//qianka.b0.upaiyun.com/images/a1cb71e49df49982dc01f9b2e5c7368a.png), 70px 52px, center);
    }

    &.icon-invalid {
      @include background-image(url(//assets.qkcdn.com/images/026c3efe1c0e4debe0a7dfc519ac1c92.png), 70px 52px, center);
    }
  }

  .icon-new {
    &.icon-download {
      margin-top: 11px;
      margin-bottom: 22px;
      height: 150px;

      @include background-image(url(//assets.qkcdn.com/images/23b0c01ab6fc834d8b69455a28030025.png), 192px 150px, center);
    }

    &.icon-invalid {
      margin-bottom: 20px;
      height: 163px;

      @include background-image(url(//assets.qkcdn.com/images/026c3efe1c0e4debe0a7dfc519ac1c92.png), 255px 163px, center);
    }
  }

  .title {
    @include font-regular();

    font-size: 14px;
    color: #3a3a3a;
    line-height: 22px;
    margin-bottom: 25px;
  }

  .sub-title,
  .tips {
    font-size: 12px;
    line-height: 17px;
    color: #b5b5b5;

    a.link {
      color: $assertive;
      text-decoration: underline;
    }
  }

  .sub-title {
    margin-top: 85px;
    text-align: left;
    padding-left: 78px;
  }

  .popup-buttons {
    position: absolute;
    bottom: 0px;
    left: 0px;
    width: 275px;

    .button.button-assertive.button-block {
      border-radius: 50px !important;
      margin: 0 30px;

      .hairline-top::before {
        visibility: hidden;
      }
    }
  }
}

.popup-open-assistant {
  height: 255px;

  .popup-buttons {
    bottom: 47px;
  }
}

.popup-upgrade-assistant {
  height: 342px;

  .popup-buttons {
    bottom: 62px;
    min-height: 40px !important;

    .button.button-assertive.button-block {
      margin: 0 57px 0 58px;
      min-height: 40px !important;
      padding: 10px 0px !important;
      border-radius: 2px !important;
    }

    .button.button-light.button-block {
      position: absolute;
      width: 56px;
      top: 58px;
      padding: 0px;
      left: 140px;
      min-height: 17px;

      span {
        font-size: 12px;
        color: #ef4f2a;
        text-decoration: underline;
      }
    }
  }
}

.popup-invalid-assistant {
  height: 310px;

  .popup-buttons {
    bottom: 30px;
    min-height: 40px !important;

    .button.button-assertive.button-block {
      margin: 0 57px 0 58px;
      min-height: 40px !important;
      padding: 10px 0px !important;
      border-radius: 2px !important;

      span {
        font-size: 13px;
      }
    }
  }
}

.popup-open-assistant,
.popup-upgrade-assistant,
.popup-invalid-assistant {
  .popup-body {
    .hairline-bottom::after {
      display: none;
    }
  }
}

// lite
.popup-open-assistant-lite {
  text-align: center;

  .popup-body {
    overflow: hidden !important;
    padding-top: 10px !important;
    padding-bottom: 62px !important;
    height: 342px;
  }

  .icon {
    margin-bottom: 20px;
    height: 163px;

    &.icon-off {
      @include background-image(url('//qianka.b0.upaiyun.com/images/a01c97e51c4cc75e7ac0a2e0bfbfb626.png'), 255px 163px, center);
    }
  }

  .title {
    line-height: 22px;
    margin-bottom: 25px;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #3A3A3A;
  }

  .tips {
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #B5B5B5;
    position: absolute;
    width: 239px;
    left: 18px;
    bottom: 10px;

    .link {
      color: $assertive;
    }
  }

  .hairline-bottom::after {
    visibility: hidden;
  }

  .popup-buttons {
    position: absolute;
    bottom: 62px;
    left: 58px;
    width: 160px;
    height: 40px;
    min-height: 40px !important;

    .button-assertive {
      border-radius: 2px !important;
      padding: 9px 10px !important;
      min-height: 40px !important;
    }
  }
}

.popup-invalid-assistant-lite {
  text-align: center;
  padding-bottom: 70px;

  .popup-body {
    padding: 10px 18px 25px !important;

    .icon {
      margin-bottom: 20px;
      height: 163px;

      &.icon-invalid {
        @include background-image(url(//qianka.b0.upaiyun.com/images/55af39fdd9f7be29b878ef2aead92e47.png), 255px 163px, center);
      }
    }

    .title {
      font-family: PingFangSC-Regular;
      font-size: 14px;
      color: #3A3A3A;
      line-height: 22px;
      margin-bottom: 0px;

      .span {
        color: $assertive;
      }
    }
  }

  .hairline-bottom::after {
    visibility: hidden;
  }

  .popup-buttons {
    position: absolute;
    bottom: 0px;
    left: 0px;
    width: 275px;
    height: 70px;

    .button {
      position: absolute;
      left: 57px;
      width: 160px;
      border-radius: 2px !important;
      min-height: 40px !important;
      box-sizing: border-box !important;
      padding: 9px 12px !important;
      font-size: 13px !important;

      .hairline-top::before,
      .hairline-right::after {
        visibility: hidden !important;
      }

      &.button-assertive {
        border: 1px solid #ea5a49 !important;
      }

      &.button-light {
        top: 55px;
        border: 1px solid #3a3a3a !important;
      }
    }
  }
}



