@import "../scss/variables";
@import '../scss/mixins';
$themeColor: '#007aff';

$navbar-z-index: 10;
$navbar-title-z-index: 12;
$navbar-button-z-index: 13;

.navbar {
  background-image: linear-gradient(-180deg, #2D2C33 0%, rgba(24,23,26,0.85) 98%) !important;

  .bar.bar-header {
    .title {
      color: #FFF;
      font-weight: 300;
      background: transparent !important;
    }

    .buttons > .button.button-icon {
      color: #FFF;

      .icon.ion-ios-arrow-back::before {
        content: ' ';
        width: 15px;
        height: 32px;
        background-image: url(//qianka.b0.upaiyun.com/images/d43eddcca1630d2637e05b61086991f8.png);
        background-size: 8px 15px;
        background-repeat: no-repeat;
        background-position: center;
        margin-left: -12px + 16px;
      }
    }

    // 导航栏菜单按钮字体
    .buttons:last-child .button {
      font-size: 13px;
    }
  }
}



