/* thin border */
.popup-body.popup-assistant {
  padding: 193px 10px 25px 10px !important;
  text-align: center;
  height: 342px;
}
.popup-body.popup-assistant.popup-unopen-assistant {
    background-image: url("//qianka.b0.upaiyun.com/images/6cfffc8517e6ce53457f010e41fd5c51.png");
    background-size: 255px 163px;
    background-position: 10px 10px;
    background-repeat: no-repeat;
}
.popup-body.popup-assistant.popup-invalid-assistant {
    background-image: url("//qianka.b0.upaiyun.com/images/0768a1cb4d4f745d30085af717565e6a.png");
    background-size: 255px 163px;
    background-position: 10px 10px;
    background-repeat: no-repeat;
    height: 310px;
}
.popup-body.popup-assistant.popup-upgrade-assistant {
    background-image: url("//qianka.b0.upaiyun.com/images/2e55daba837c230dca9db2886b498149.png");
    background-size: 255px 163px;
    background-position: 10px 10px;
    background-repeat: no-repeat;
}
.popup-body.popup-assistant .text {
    font-size: 14px;
    line-height: 22px;
    color: #3A3A3A;
    margin-bottom: 25px;
    font-family: "PingFangSC-Regular", "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif;
    font-weight: 400;
}
.popup-body.popup-assistant .btn-open, .popup-body.popup-assistant .btn-download, .popup-body.popup-assistant .btn-upgrade {
    width: 160px;
    height: 40px;
    min-height: 40px;
    border-radius: 2px;
    margin-bottom: 20px;
    font-size: 13px;
}
.popup-body.popup-assistant .btn-download {
    margin-bottom: 0;
}
.popup-body.popup-assistant .tips {
    font-family: "PingFangSC-Regular", "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    color: #B5B5B5;
}



