// Colors
$light:                           #fff !default;
$stable:                          #f5f5f5 !default;
$positive:                        #4a90e2 !default;
$calm:                            #11c1f3 !default;
$balanced:                        #44cc00 !default;
$energized:                       #ffbd17 !default;
$assertive:                       #ef4f2a !default;
$royal:                           #b5b5b5 !default;
$dark:                            #484746 !default;

$default-page-content-color:      #F5F5F5;
$default-border-color:            #EEE;
$incognito-mode-z-index:          9999;

// For Navbar Overwrite
$ios-transition-duration:              500ms !default;
$ios-transition-timing-function:       cubic-bezier(0.36, 0.66, 0.04, 1) !default;
$android-transition-duration:          200ms !default;
$android-transition-timing-function:   cubic-bezier(0.4, 0.6, 0.2, 1) !default;

// Font
$font-stack: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif;



