@import 'variables';

@mixin background-image($url, $size: 100%, $pos: center) {
  background-image: $url;
  background-size: $size;
  background-position: $pos;
  background-repeat: no-repeat;
}

/* thin border */
@mixin thin-border-top($color, $radius) {
  position: relative;

  &::before {
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    border-top: 1px solid $color;
    border-radius: $radius;
    transform-origin: 0 0;
    z-index: 0;
    box-sizing: border-box;

    @media only screen and (-webkit-min-device-pixel-ratio: 2) {
      height: 200%;
      width: 200%;
      transform: scale(0.5);
      border-radius: ($radius * 2);
    }
  }

  &::after {
    border-top: none;
  }
}

@mixin thin-border-bottom($color, $radius) {
  position: relative;

  &::before {
    border-bottom: none;
  }

  &::after {
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    border-bottom: 1px solid $color;
    border-radius: $radius;
    transform-origin: 0 0;
    z-index: 0;
    box-sizing: border-box;

    @media only screen and (-webkit-min-device-pixel-ratio: 2) {
      height: 200%;
      width: 200%;
      transform: scale(0.5);
      border-radius: ($radius * 2);
    }
  }
}

// Margin & Padding

@mixin m($val) { margin: $val !important; }

@mixin mt($val) { margin-top: $val !important; }

@mixin mb($val) { margin-bottom: $val !important; }

@mixin ml($val) { margin-left: $val !important; }

@mixin mr($val) { margin-right: $val !important; }

@mixin mv($val) { margin: $val auto; }

@mixin mh($val) { margin: auto $val !important; }

@mixin p($val) { padding: $val !important; }

@mixin pt($val) { padding-top: $val !important; }

@mixin pb($val) { padding-bottom: $val !important; }

@mixin pl($val) { padding-left: $val !important; }

@mixin pr($val) { padding-right: $val !important; }

@mixin pv($val) { padding: $val auto; }

@mixin ph($val) { padding: auto $val !important; }

// transition
@mixin transition($transition...) {
  -webkit-transition: $transition;
  transition: $transition;
}

@mixin transition-delay($transition-delay) {
  -webkit-transition-delay: $transition-delay;
  transition-delay: $transition-delay;
}

@mixin transition-duration($transition-duration) {
  -webkit-transition-duration: $transition-duration;
  transition-duration: $transition-duration;
}

@mixin transition-timing-function($transition-timing) {
  -webkit-transition-timing-function: $transition-timing;
  transition-timing-function: $transition-timing;
}

@mixin transition-property($property) {
  -webkit-transition-property: $property;
  transition-property: $property;
}

@mixin hairline($position: bottom) {
  content: '';
  position: absolute;
  background-color: $default-border-color;
  display: block;

  @if $position == left {
    left: 0;
    top: 0;
    bottom: auto;
    right: auto;
    height: 100%;
    width: 1px;
    transform-origin: 0 50%;
    -webkit-transform-origin: 0 50%;
    transform: scaleX(0.5);
    -webkit-transform: scaleX(0.5);
  }

  @else if $position == right {
    left: auto;
    top: 0;
    bottom: auto;
    right: 0;
    height: 100%;
    width: 1px;
    transform-origin: 100% 50%;
    -webkit-transform-origin: 100% 50%;
    transform: scaleX(0.5);
    -webkit-transform: scaleX(0.5);
  }

  @else if $position == top {
    left: 0;
    top: 0;
    bottom: auto;
    right: auto;
    height: 1px;
    width: 100%;
    transform-origin: 0% 0%;
    -webkit-transform-origin: 0% 0%;
    transform: scaleY(0.5);
    -webkit-transform: scaleY(0.5);
  }

  @else { // $position == bottom
    left: 0;
    top: atuo;
    bottom: 0;
    right: auto;
    height: 1px;
    width: 100%;
    transform-origin: 0% 100%;
    -webkit-transform-origin: 0% 100%;
    transform: scaleY(0.5);
    -webkit-transform: scaleY(0.5);
  }
}

@mixin font-light() {
  font-family: "PingFangSC-Light", $font-stack;
}

@mixin font-regular() {
  font-family: "PingFangSC-Regular", $font-stack;
  font-weight: 400;
}

@mixin font-medium() {
  font-family: "PingFangSC-Medium", $font-stack;
  font-weight: 500;
}

@mixin font-bold() {
  font-family: "PingFangSC-Bold", $font-stack;
  font-weight: 600;
}

@mixin font-semibold() {
  font-family: "PingFangSC-Semibold", $font-stack;
}

@mixin font-hzgb() {
  font-family: "hzgb", $font-stack;
}

@mixin font-AppleSystemUIFont() {
  font-family: "AppleSystemUIFont", $font-stack;
}

@mixin font-SFNSDisplay-Regular() {
  font-family: "SFNSDisplay-Regula", $font-stack;
}



