@import "~scss/mixins";

// 任务进行中
.popup-task-ongoing {
  .popup-body {
    height: 328px;
    padding: 10px !important;

    .img-wrap {
      height: 163px;
      margin-bottom: 20px;

      img.top {
        width: 100%;
        height: auto;
      }
    }

    .title, .desc {
      @include font-regular();

      font-size: 13px;
      line-height: 1.54;
      text-align: center;
      color: #3a3a3a;
    }
  }

  .popup-buttons {
    width: 160px;
    min-height: 40px !important;
    position: absolute;
    left: 58px;
    bottom: 30px;

    .button {
      background-color: #EF4F2A !important;
      border-radius: 2px !important;
      min-height: 40px !important;
      height: 40px !important;
      line-height: 40px !important;
      padding: 0 !important;

      span {
        font-family: PingFang-SC-Medium;
        font-size: 13px;
        color: #FFFFFF;
      }
    }
  }
}

// 下载线上助手
.popup-download-assistant {
  text-align: center;

  .popup-body {
    height: 365px;

    .remind {
      margin-bottom: 25px;
      margin-top: 9px;

      span {
        display: block;
        margin-bottom: 4px;
        font-family: PingFangSC-Regular;
        font-size: 16px;
        color: #3A3A3A;
        height: 22px;
        line-height: 22px;
      }
    }

    .bg {
      height: 128px;
    }

    .app-icon {
      position: absolute;
      left: 53px;
      top: 143px;
      width: 58px;
      height: 58px;
      border-radius: 14px;
    }

    .app-name {
      position: absolute;
      top: 144px;
      left: 119px;
      font-family: PingFangSC-Regular;
      font-size: 8px;
      color: #333333;
      height: 11px;
      line-height: 11px;
    }

    .app-developer {
      position: absolute;
      top: 157px;
      left: 119px;
      font-family: PingFangSC-Regular;
      font-size: 6px;
      color: #333333;
      height: 8px;
      line-height: 8px;

      img {
        margin-left: 3px;
        vertical-align: middle;
      }
    }

    .arrow {
      width: 2px;
      height: 4px;
    }

    .bottom-remind {
      position: absolute;
      top: 318px;
      left: 66px;
      font-family: PingFangSC-Regular;
      font-size: 12px;
      color: #B5B5B5;
    }

    .hairline-bottom {
      visibility: hidden !important;
    }
  }

  .popup-buttons {
    width: 160px;
    position: absolute;
    top: 266px;
    left: 58px;

    .button {
      background-color: #EF4F2A !important;
      border-radius: 2px !important;
      min-height: 40px !important;
      height: 40px !important;
      line-height: 40px !important;
      padding: 0 !important;

      span {
        font-family: PingFang-SC-Medium;
        font-size: 13px;
        color: #FFFFFF;
      }
    }
  }
}

// 企业助手设置引导
.popup-assistant-guide {
  .title {
    position: absolute;
    width: 100%;
    text-align: center;
    font-size: 16px;
    line-height: 22px;

    &.single-line {
      top: 30px;
    }

    &.multi-line {
      top: 22px;
    }
  }

  #swipe-assistant-guide-wrap {
    #swipe-assistant-guide {
      overflow: hidden;
      visibility: hidden;
      position: relative;

      .swipe-wrap {
        overflow: hidden;
        position: relative;

        .swipe-item {
          .step {
            width: 275px;
            height: 226px;

            &.step-1 {
              @include background-image(url('//qianka.b0.upaiyun.com/images/dfc9bd0178f8fdd88b979c2889eadeec.png'), 275px 226px, center);
            }

            &.step-2 {
              @include background-image(url('//qianka.b0.upaiyun.com/images/f92fc1a6d04c2f82cb5e4a3db5d5d264.png'), 275px 226px, center);
            }

            &.step-3 {
              @include background-image(url('//qianka.b0.upaiyun.com/images/3af6d07eace182ecc77522d10074cab4.png'), 275px 226px, center);
            }

            &.step-4 {
              @include background-image(url('//qianka.b0.upaiyun.com/images/20ccf85691ae7e2f49bbd5c8d179a627.png'), 275px 226px, center);
            }
          }
        }
      }
    }

    .circles {
      text-align: center;
      height: 5px;
      width: 35px;
      margin: 16px auto;

      .circle {
        float: left;
        width: 5px;
        height: 5px;
        border-radius: 5px;
        margin-right: 5px;
        background: #E0E0E0;

        &.active {
          background: #FD5F4E;
        }

        &:last-of-type {
          margin-right: 0;
        }
      }
    }
  }

  .popup-body {
    padding: 22px 0 0 0 !important;
    max-height: 285px;
  }
}

// 绑定芝麻信用
.popup-bind-zhima {
  .icon {
    margin-top: -6px;
    height: 28px;
    margin-bottom: 14px;

    img {
      width: 24px;
      height: 28px;
      margin-bottom: 0;
    }
  }

  .title {
    font-family: PingFangSC-Medium;
    height: 18px;
    line-height: 18px;
    margin-bottom: 10px;
    font-size: 13px;
    color: #888888;
    letter-spacing: 0;

    .line {
      width: 19px;
      height: 1px;
      transform: translate3d(0, -5px, 0);
      -webkit-transform: translate3d(0, -5px, 0);
    }
  }

  .desc {
    font-size: 13px;
    color: #888888;
    line-height: 20px;
    margin-bottom: 4px;
  }

  .popup-buttons {
    .button-light {
      height: 50px;
      background-color: #04C9B3 !important;

      span {
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #FFFFFF;
      }
    }
  }
}

// 开启推送弹层
.popup-enable-push {
  text-align: center;

  .title, .sub-title {
    font-size: 16px;
    line-height: 22px;
  }

  .title {
    margin-bottom: 4px;
  }

  .sub-title {
    margin-bottom: 20px;
  }

  div.img {
    height: 160px;
    margin-bottom: 8px;

    @include background-image(url('//qianka.b0.upaiyun.com/images/9d51a105836202127ed502e43d669ff9.png'), 224px 160px, center);
  }

  .popup-body {
    .hairline-bottom::after {
      display: none;
    }
  }

  .popup-buttons {
    height: 70px;
    width: 161px;
    margin-left: 57px;

    .button.button-block.button-assertive {
      height: 40px;
      min-height: 40px;
      border-radius: 2px;
      font-size: 13px;
      line-height: 13px;
    }
  }
}



