// 完成任务奖励通知弹层
.popup-task-reward {
  text-align: center;

  .img-task-finished {
    width: 220px;
    height: 143px;
    margin-top: -6px;
  }

  .reward {
    color: #FD6344;
    // font-size: 88px;
    // line-height: 124px;

    span {
      display: inline-block;
    }

    .rmb {
      font-size: 28px;
      transform: translate3d(0, -1px, 0);
      -webkit-transform: translate3d(0, -1px, 0);
    }

    .amount {
      font-size: 88px;
      line-height: 124px;
    }

    .yuan {
      font-size: 28px;
      transform: translate3d(0, -4px, 0);
      -webkit-transform: translate3d(0, -4px, 0);
    }
  }

  .task-info {
    position: relative;
    margin-top: 15px;
    margin-bottom: 35px;

    div {
      color: #A0A8AE;
      height: 34px;

      &.line {
        height: 1px;
        width: 100%;
        position: absolute;
        left: 0;
        top: 17px;
        background-image: url('//qianka.b0.upaiyun.com/images/62bd02c317cca896e436ca64fafb83b2.png');
        background-repeat: repeat-x;
        background-position: 0, 0;
        background-size: 7px 1px;
        transform: scale(1, 0.5);
        -webkit-transform: scale(1, 0.5);
        z-index: 1;
      }

      &.task-type, &.task-name {
        top: 0;
        z-index: 100;
        background: #fff;
        font-size: 12px;
        line-height: 34px;
        height: 34px;
      }

      &.task-type {
        position: absolute;
        left: 0;
        padding-right: 8px;
      }

      &.task-name {
        position: absolute;
        right: 0;
        padding-left: 8px;
        color: #FD6344;
      }
    }
  }

  .popup-buttons {
    .button-assertive {
      height: 50px;
      background-color: #FF5236 !important;
    }
  }
}

