    img {
      width: 220px;
      height: 143px;
      margin-bottom: 9px;
      vertical-align: middle;
    }

    .reward {
      font-size: 14px;
      color: #FD6344;
      margin-bottom: 20px;

      .font-44 {
        font-size: 44px;
        display: inline-block;
        margin: 0 2px;
        line-height: 62px;
      }
    }

    .credit-info {
      position: relative;
      font-size: 12px;
      width: 100%;
      height: 17px;

      .line {
        height: 1px;
        width: 100%;
        position: absolute;
        left: 0;
        top: 10px;
        background-image: url('//qianka.b0.upaiyun.com/images/62bd02c317cca896e436ca64fafb83b2.png');
        background-repeat: repeat-x;
        background-position: 0, 0;
        background-size: 7px 1px;
        transform: scale(1, 0.5);
        -webkit-transform: scale(1, 0.5);
        z-index: 1;
      }

      .credit-type {
        color: #A0A8AE;
