}

// 完成高额任务弹层
// 完成收徒任务 1 + 3
// 提现成功弹层
.popup-lotterty-get-bets {
  border-radius: 4px;
  text-align: center;
  font-family: PingFangSC-Light;

  .popup-body {
    padding: 20px 18px !important;
    background-image: linear-gradient(90deg, #8D55D2 0%, #6831B7 100%);
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    -webkit-border-top-left-radius: 4px;
    -webkit-border-top-right-radius: 4px;

    img {
      width: 220px;
      height: 130px;
      vertical-align: bottom;
    }

    .win-info {
      width: 100%;

      .win-money {
        font-size: 14px;
        color: #F9D53F;
        margin-bottom: 10px;

        .font-big {
          font-size: 44px;
          color: #F9D53F;
          line-height: 62px;
        }
      }

      .win-desc {
        position: relative;
        width: 100%;
        height: 17px;
        margin-bottom: 5px;

        .line {
          float: left;
          height: 1px;
          width: 57%;
          position: absolute;
          left: 23%;
          top: 10px;
          background-image: url('//qianka.b0.upaiyun.com/images/851a87124256356b664ed191d71f8ff2.png');
          background-repeat: repeat-x;
          background-position: 0, 0;
          background-size: 7px 1px;
          transform: scale(1, 0.5);
          -webkit-transform: scale(1, 0.5);
          z-index: 1;
        }

        .win-tag {
          float: left;
          position: absolute;
          left: 0;
          padding-right: 8px;
          font-size: 12px;
          color: #FFFFFF;
          z-index: 2;
        }

        .win-price {
          float: left;
          position: absolute;
          right: 0;
          padding-left: 8px;
          font-size: 12px;
          color: #F9D53F;
          z-index: 2;

          .add-symbol {
            font-size: 8px;
          }
        }
      }

      .win-desc:last-child {
        margin-bottom: 0px;

        .line {
          width: 54%;
        }
      }
    }
  }

  .popup-buttons {
    .button {
      padding-top: 15px !important;
      padding-bottom: 15px !important;
      font-family: PingFangSC-Regular;
      color: #8747C9;
    }
  }

  .button-close {
    .ion-ios-close-empty::before {
      background-image: url('//qianka.b0.upaiyun.com/images/171be86d54a4421e0733ba2999857f11.png');
    }
  }
}

// 新的概念版的完成任务弹层样式
@mixin display-flex {
  display: -webkit-box;           /* OLD - iOS 6-, Safari 3.1-6 */
  display: -moz-box;              /* OLD - Firefox 19- (doesn't work very well) */
  display: -ms-flexbox;           /* TWEENER - IE 10 */
  display: -webkit-flex;          /* NEW - Chrome */
  display: flex;                  /* NEW, Spec - Opera 12.1, Firefox 20+ */
}

@mixin order($value) {
  -webkit-box-ordinal-group: $value;   /* OLD - iOS 6-, Safari 3.1-6 */
  -moz-box-ordinal-group: $value;      /* OLD - Firefox 19- */
  -ms-flex-order: $value;              /* TWEENER - IE 10 */
  -webkit-order: $value;               /* NEW - Chrome */
  order: $value;                       /* NEW, Spec - Opera 12.1, Firefox 20+ */
}

@mixin justify-content() {
  justify-content: center;
  -webkit-justify-content: center;
}

@mixin flex-direction($direction) {
  flex-direction: $direction;
  -webkit-flex-direction: $direction;
}

@mixin flex ($val) {
  -webkit-box-flex: $val;      /* OLD - iOS 6-, Safari 3.1-6 */
  -webkit-flex: $val;          /* Chrome */
  flex: $val;                  /* NEW, Spec - Opera 12.1, Firefox 20+ */
}

.popup-task-reward-new {
  .popup-body {
    height: 400px;
    padding: 203px 10px 24px 10px !important;

    @include background-image(url('//qianka.b0.upaiyun.com/images/2fd4715c30b99deafc715b9bb8df3597.png'), 255px 163px, 10px 10px);

    .tips {
      font-family: PingFangSC-Regular;
      font-size: 14px;
      color: #EF4F2A;
      text-align: center;
      margin-bottom: 7px;
    }

    .reward {
      @include display-flex();

      @include justify-content();

      color: #EF4F2A;
      margin-bottom: 20px;
      justify-content: center;

      .rmb, .amount, .yuan {
        @include display-flex();

        @include flex-direction(column-reverse);
      }

      .rmb {
        @include order(1);

        transform: translate3d(0, -1px, 0);
        -webkit-transform: translate3d(0, -1px, 0);
      }

      .amount {
        @include order(2);

        font-family: PingFangSC-Semibold;
        font-size: 36px;
        color: #EF4F2A;
        line-height: 36px;
      }

      .yuan {
        @include order(3);

        transform: translate3d(0, -3%, 0);
        -webkit-transform: translate3d(0, -3%, 0);
      }
    }

    .task-info {
      @include display-flex();

      color: #EF4F2A;
      padding: 0 10px;

      .task-type, .task-name {
        font-family: PingFangSC-Light;
        font-size: 12px;
      }

      .task-type {
        @include order(1);

        color: #A0A7AD;
        padding-right: 4px;
      }

      .line {
        @include order(2);

        @include flex(1);

        background-image: url('http://qianka.b0.upaiyun.com/images/62bd02c317cca896e436ca64fafb83b2.png');
        background-repeat: repeat-x;
        background-position: 0, 0;
        background-size: 7px 1px;
        z-index: 1;
      }

      .task-name {
        @include order(3);

        color: #EF4F2A;
        padding-left: 4px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: 80px;
      }
    }

    .hairline-bottom::after {
      display: none;
    }
  }

  .popup-buttons {
    height: 64px;
    min-height: 64px;
    margin-top: -64px;
    padding: 0 58px !important;

    .button {
      height: 40px;
      min-height: 40px !important;
      border-radius: 2px !important;

      &.button-block.button-assertive {
        font-size: 13px;
        padding: 10px !important;
      }
    }
  }
}



