@import "../../scss/mixins";

.popup-container {
  .show_setting_file {
    .popup-buttons {
      padding-left: 162px;

      .button-light {
        color: #4a90e2;
      }

      .hairline-right {
        display: none;
      }
    }
  }
}

.popup_show_qrcode {
  height: 362px;

  .qrcode {
    margin: 18px auto 20px;
    text-align: center;
    position: relative;
    padding: 3px;
    width: 186px;
    height: 186px;
    border: 2px solid #DDDDDD;

    .across-space {
      position: absolute;
      width: 166px;
      height: 2px;
      background-color: #FFFFFF;

      &.top {
        top: -2px;
        left: 8px;
      }

      &.bottom {
        bottom: -2px;
        left: 8px;
      }
    }

    .vertical-space {
      position: absolute;
      width: 2px;
      height: 166px;
      background-color: #FFFFFF;

      &.right {
        right: -2px;
        top: 8px;
      }

      &.left  {
        left: -2px;
        top: 8px;
      }
    }

    img {
      width: 176px;
      height: 176px;
      -webkit-touch-callout: default;
    }
  }

  .remind {
    margin: 0 auto;
    text-align: center;
    width: 180px;
    font-size: 13px;
    color: #3a3a3a;

    @include font-regular();

    &.last {
      margin-bottom: 10px;
    }
  }

  .remind-or {
    text-align: center;
    font-size: 13px;
    color: #b5b5b5;

    @include font-regular();
  }
}



