@import '~scss/mixins';

.task-action-list {
  padding-top: 24px;

  .task-action-list-title {
    height: 36px;
    font-size: 20px;
    line-height: 36px;
    color: #ddd;
    padding: 0 16px;
    display: inline-block;
    vertical-align: bottom;
  }

  .task-action-list-subtitle {
    font-size: 13px;
    line-height: 29px;
    height: 29px;
    color: #DDDDDD;
    display: inline-block;
    vertical-align: bottom;

    @include font-regular();
  }

  .task-action {
    position: relative;
    height: 80px;
    padding: 14px 14px 14px 76px;

    .icon-wrapper {
      position: absolute;
      left: 14px;
      top: 14px;

      .icon {
        border: 1px solid #ddd;
        border-radius: 12px;
        width: 100%;
        height: 100%;

        &.border-blue {
          border: 1px solid #4A90E2;
        }

        &.border-green {
          border: 1px solid #68bb8b;
        }

        img, .icon-no-img {
          position: absolute;
          width: 96px;
          height: 96px;
          left: 4px;
          top: 4px;
          border-radius: 9px;
        }

        .icon-no-img {
          padding: 14px 0px;
          background-color: #EFF9F4;
          text-align: center;

          @include font-semibold();

          font-size: 24px;
          color: #68BB8B;
          line-height: 34px;

          &.easy {
            background-color: #ECF5FF;
            color: #4A90E2;
          }
        }
      }
    }

    .app-name {
      font-size: 16px;
      line-height: 22px;
      color: #3A3A3A;
      margin-top: 4px;
      margin-bottom: 6px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      max-width: 200px;

      @include font-regular();
    }

    .desc {
      font-size: 13px;
      font-weight: lighter;
      line-height: 18px;
      color: #B5B5B5;

      .ongoing {
        font-size: 13px;
        color: #4A90E2;

        @include font-medium();
      }

      .push-tips {
        font-size: 16px;
        color: #3A3A3A;
        height: 38px;
        line-height: 38px;

        @include font-regular();
      }
    }

    .no-task-desc {
      font-family: PingFangSC-Regular;
      font-size: 14px;
      color: #3A3A3A;
      line-height: 40px;
    }

    .reward-wrap {
      position: absolute;
      top: 0px;
      right: 14px;
      height: 80px;
      line-height: 80px;
      color: #EF4F2A;

      @include font-medium ();

      .reward-content {
        display: flex;

        .plus {
          order: 1;
          padding-right: 2px;
        }

        .reward {
          order: 2;
        }

        .yuan {
          order: 3;
          padding-left: 2px;
        }

        .plus, .yuan {
          font-size: 11px;
        }

        .reward {
          font-size: 22px;
        }

        .plus {
          transform: translate3d(0, 3%, 0);
        }

        .yuan {
          transform: translate3d(0, 2.5%, 0);
        }
      }
    }
  }
}



