/* 挂载到 vonic components 内部的样式写在这里 */

@import "mixins";

/* 首页入口 */
.qk-entrances {
  > .row:first-of-type > .hairline-top,
  > .hairline-bottom {
    display: none;
  }

  .hairline-top, .hairline-bottom, .hairline-left, .hairline-right {
    &::before, &::after {
      background: #EEE !important;
    }
  }
}

.qk-entrance {
  position: relative;
  height: 88px;
  padding: 25px 0 0 68px;
  text-align: left;
  z-index: 2;

  &:active {
    background-color: rgba(#EA5A49, 0.02);
  }

  .icon {
    position: absolute;
    top: 28px;
    left: 20px;
    width: 34px;
    height: 34px;
  }

  .title {
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 2px;
    display: inline-block;
    color: $dark;
    position: relative;

    .tag {
      position: absolute;
      height: 14px;
      top: -11px;
      padding: 0 5px;
      margin-left: 2px;
      left: 52px;
      border-radius: 2px;
      background-size: 100% 100%;

      img {
        height: 14px;
      }

      .text {
        position: absolute;
        top: -3px;
        left: 10px;
        font-size: 8px;
        color: #FFFFFF;

        @include font-medium();
      }
    }
  }

  .desc {
    font-size: 12px;
    line-height: 17px;
    color: $royal;
  }
}

.qk-withdraw-detail-input {
  .hl-item-borderless-top .hairline-top::before {
    visibility: hidden;
  }

  .hl-item-borderless-bottom .hairline-bottom::after {
    visibility: hidden;
  }

  .button {
    border-radius: 2px;
  }
}

[qk-progress-bar] {
  top: 44px !important;
}

.modal-open-1000 {
  .modal-backdrop {
    z-index: 1000;
  }
}

.modal-open-1000 {
  &.modal-open {
    pointer-events: auto !important;
  }

  &.popup-open {
    pointer-events: auto !important;
  }
}

.popup-container {
  width: 375px;
}

// 输入框公共样式
.von-input-1 {
  &.von-input-wrapper .von-input {
    padding-left: 16px;
  }

  .input-label {
    width: auto;
    padding-right: 16px;
  }
}

/* 全局样式覆盖 */

// 字体
body {
  &[font="pingfang"] {
    @include font-light();

    .font-regular {
      @include font-regular();
    }

    .font-medium {
      @include font-medium();
    }

    .font-bold {
      @include font-bold();
    }

    .font-hzgb {
      @include font-hzgb();
    }
  }
}

// 页面背景色
.page .page-content {
  background-color: $default-page-content-color;
}

// 输入框字体
textarea, input[type] {
  font-weight: 300;
  color: #484746;

  &::-webkit-input-placeholder {
    color: #b5b5b5;
  }
}

// $popup样式
.popup-body {
  padding-top: 26px !important;
  padding-bottom: 26px !important;

  .hairline-bottom::after {
    background-color: #EEEEEE;
  }

  // 关闭按钮
  .button-close .ion-ios-close-empty {
    position: absolute;
    top: -1px;
    right: 10px;
    width: 14px;
    height: 14px;

    &::before {
      content: ' ';
      width: 14px;
      height: 14px;

      @include background-image(url(//qianka.b0.upaiyun.com/images/95991e659499d7aa41b64e20d249c876.png));
    }
  }
}

// $popup 按钮样式
.popup-buttons > .button {
  @include font-semibold();

  .hairline-right::after {
    background-color: #EEEEEE;
  }

  &:last-child {
    .hairline-right::after {
      visibility: hidden;
    }
  }
}

// $modal导航栏样式
.modal .bar.bar-dark {
  background-color: rgba(#2D2C33, 0.98) !important;

  h1.title {
    font-weight: 300;
  }
}

// 按钮响应
.button-assertive {
  &:active {
    background-color: #D25041 !important;
  }
}

.button-positive {
  &:active {
    background-color: #447DC4 !important;
  }
}

.button-light {
  &:active {
    background-color: #fbfbfb !important;
  }
}

.text-ellipsis {
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: block;
}

// thin horizontal line (dividor)
.hairline-stand-alone {
  height: 1px;
  display: block;
  background-color: #eee;
  transform-origin: 0 50%;
  -webkit-transform-origin: 0 50%;
  transform: scaleY(0.5);
  -webkit-transform: scaleY(0.5);
}

// thin vertival line (dividor)
.hairline-stand-alone.vertical {
  height: 100%;
  width: 1px;
  background-color: #eee;
  transform-origin: 50% 0;
  -webkit-transform-origin: 50% 0;
  transform: scaleX(0.5);
  -webkit-transform: scaleX(0.5);
}

// utility classes
.divider {
  height: 10px;
}

.clearfix {
  clear: both;
}

.shift-right {
  margin-right: -16px !important;
}

.right {
  float: right !important;
}

.left {
  float: left !important;
}



