@import '~scss/mixins';
// 下注中奖弹层
.popup-lotterty-win {
  border-radius: 4px;
  text-align: center;
  font-family: PingFangSC-Light;

  .popup-body {
    background-image: linear-gradient(90deg, #6831B7 0%, #8D55D2 100%);
    padding: 20px 18px 24px 18px !important;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    -webkit-border-top-left-radius: 4px;
    -webkit-border-top-right-radius: 4px;

    img {
      width: 220px;
      height: 130px;
      vertical-align: bottom;
    }

    .win-info {
      width: 100%;

      .win-money {
        font-size: 14px;
        color: #F9D53F;
        margin-bottom: 10px;

        .font-big {
          font-size: 44px;
          color: #F9D53F;
          line-height: 62px;
        }
      }

      .win-desc {
        width: 152px;
        height: 21px;
        margin: 0 auto;
        line-height: 21px;
        background: rgba(255, 255, 255, 0.1);
        border-radius: 100px;
        font-size: 12px;
        color: #FFFFFF;
      }
    }
  }

  .popup-buttons {
    .button {
      padding-top: 15px !important;
      padding-bottom: 15px !important;
      font-family: PingFangSC-Regular;
      color: #8747C9;
    }
  }
}

.popup-container > .open_rebate_entrance {
  height: 381px;

  .title {
    text-align: center;
    height: 26px;
    line-height: 26px;
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #3A3A3A;

    span {
      color: #ffa70f;
    }
  }

  .pic {
    margin-top: 25px;
    text-align: center;

    img {
      width: 219px;
      height: 174px;
    }
  }

  .hairline-bottom::after {
    visibility: hidden;
  }

  .popup-buttons {
    margin-left: 58px;
    width: 160px;

    .button {
      width: 50px;
      min-height: 40px;
      height: 40px;
      line-height: 15px;
      background-color: #FFA70F !important;
      border-radius: 2px;

      span {
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #FFFFFF;
      }
    }
  }
}

.popup-national-event {
  height: 312px;

  @include font-regular();

  .popup-body {
    height: 242px;
    font-size: 13px;
    padding-left: 30px !important;
    padding-right: 30px !important;

    .hairline-bottom::after {
      visibility: hidden;
    }

    .title {
      text-align: center;
      margin-top: 4px;
      margin-bottom: 10px;
      font-size: 14px;
      color: #3A3A3A;
      height: 22px;
      line-height: 22px;
    }

    .time {
      margin-bottom: 20px;
      color: #3A3A3A;
      height: 20px;
      line-height: 20px;

      span {
        color: #EF4F2A;

        @include font-semibold();
      }
    }

    .desc {
      margin-bottom: 10px;
    }
  }

  .popup-buttons {
    width: 160px;
    margin-left: 58px;

    .button {
      min-height: 40px !important;
      padding: 0 !important;
      height: 40px;
      width: 160px !important;
      display: inline;
      background: #EF4F2A !important;
      border-radius: 2px !important;

      span {
        font-size: 13px;
        color: #FFFFFF;

        @include font-semibold();
      }
    }
  }
}

.popup-open > .backdrop {
  background-color: rgba(0, 0, 0, 0.65);
}

.open_rebate_entrance {
  .popup-body {
    padding-top: 35px;
  }
}



