// reset Loading & Toast style
.loading-container {
  .loading {
    padding: 12px 26px !important;
    border-radius: 2px;
  }

  .spinner {
    margin-bottom: 6px;
    padding-top: 2px;
  }

  .spinner.spinner-ios {
    width: 100%;
    height: 21px;
  }

  .spinner svg {
    width: 21px !important;
    height: 21px !important;
    stroke: #FFF;
    fill: #FFF;
  }

  .tips {
    font-size: 14px;
    line-height: 20px;
  }
}



