@font-face {
  font-family: 'hzgb';
  src: url('font/hzgb.eot');
  src:
    url('font/hzgb.eot') format('embedded-opentype'),
    url('font/hzgb.woff') format('woff'),
    url('font/hzgb.ttf') format('truetype');
}

@font-face {
  font-family: 'giorgio';
  src: url('./font/giorgio.eot');
  src:
    url('./font/giorgio.eot') format('embedded-opentype'),
    url('./font/giorgio.woff') format('woff'),
    url('./font/giorgio.ttf') format('truetype');
}

@font-face {
  font-family: 'GiorgioSans-Medium';
  src: url('./font/GiorgioSans-Medium.ttf');
  src:
    url('./font/GiorgioSans-Medium.eot') format('embedded-opentype'),
    url('./font/GiorgioSans-Medium.woff') format('woff'),
    url('./font/GiorgioSans-Medium.ttf') format('truetype');
}

@font-face {
  font-family: 'DINMittelschriftStd';
  src: url('./font/DINMittelschriftStd.otf');
}



